ecialmente para evitar a disponibilizao de tais contedos na internet.\n7. Contrato, Servios do SeuApp.com\n\nAo configurar uma conta, voc adquire o direito de utilizar a oferta gratuita do SeuApp.com (mdulo bsico) enquanto durar o contrato.\nComo parte do mdulo bsico  possvel criar um aplicativo web. Um aplicativo web  uma verso web de um aplicativo nativo ou de um site otimizado para dispositivos mveis. O aplicativo web  baseado em padres da internet, tais como HTML, CSS etc e  acessado atravs de um navegador. Ele  otimizado para uso em dispositivos mveis. Os recursos especiais dos dispositivos mveis (GPS, cmera etc.) no podem ser utilizados. Os aplicativos mveis so armazenados em um site do SeuApp.com.\nAlm disso, voc pode aproveitar os recursos pagos da plataforma online, como a criao de aplicativos nativos.\nUm app nativo ser criado pelo SeuApp.com a partir do aplicativo web para as app stores mveis selecionadas (Apple App Store, Google Play, Blackberry App Store etc.) e publicada sob a marca do SeuApp.com. Aplicativos nativos tambm pode acessar e usar recursos dos dispositivos mveis (GPS, cmera etc.). A deciso de aceitar a publicao do app nativo na App Store  de responsabilidade nica e exclusiva do operador da app store. O SeuApp.com no tem influncia sobre essa deciso. Ao submeter o aplicativo nativo para as app stores, o aplicativo fica sujeito aos termos e condies de cada app store, que podem ser visualizados nas respectivas app stores.\nOs aplicativos web, aplicativos nativos e outros servios so entregues apenas em cdigo executvel, sem documentao. Voc no tem direito ao cdigo fonte, rascunhos, design ou outras informaes.\nO escopo exato de cada oferta est descrito detalhadamente na platorma online. O SeuApp.com pode expandir ou restringir as ofertas na plataforma online a qualquer momento. Voc no tem direito a exigir que determinadas ofertas estejam disponveis na plataforma online no futuro. Ofertas contratadas permanecem disponveis durante a durao do contrat